@import "../common/styles/sass/variables";
@import "../common/styles/sass/mixins";

.footerBlock {
  width: 100%;
  height: 100%;
  color: $primaryColor;
  padding-bottom: 20px;
  //background: linear-gradient(45deg, #292929 25%, transparent 25%, transparent 75%, #292929 75%), linear-gradient(45deg, #292929 25%, transparent 25%, transparent 75%, #292929 75%) 0.1875em 0.1875em, radial-gradient(at 50% 0, #484847, #090909);
  //background-size: 0.375em 0.375em, 0.375em 0.375em, 100% 100%;
  text-align: center;

}

.footerContainer {
  flex-direction: column;
  justify-content: flex-start;

}
.items {
  width: 100%;
  display: flex;
  justify-content: center;
  font-size: 50px;
  margin-bottom: 20px;

}

.codeWars {
  width: 50px;
  height: 50px;
  border-radius: 25%;
  background-color: #c51111;

}

.linkedin {
  background-color: white;
  border-radius: 25%;

}

.gitHub {
  background: white;

}

.telegram {
  color: white;
  background: #4d96da;
  border-radius: 50%;
}


