.navBlock {
  position: fixed !important;
  z-index: 9999;
  padding: 0;
  margin: 0;
  background: #2f2d2d;
  font-family: Montserrat;
  //font-size: 16px;
  //overflow: hidden;
  width: 100%;
  //height: 100px;
  //opacity: .7;

  //@media screen and(max-width: 576px){
  //  width: 300px;
  //  font-size: 16px;
  ////}
  @media screen and(max-width: 576px){
    width: 300px;
    font-size: 10px;
  }
  .container {
    //background-color: #c51111;
    display: flex;
    justify-content: space-evenly;
    //align-items: center;
    //  //top: 43px;
    //  position: fixed;
    //  z-index: 9999;
    //  //left: 50%;
    width: 100%;
      //transform: translate(-50%, -50%);
      //background: $bgColor;
    //  border-radius: 5px;
    //
    .text {
      position: relative;
      //    color: #fff;
      font-weight: bold;
          //font-size: 16px;
      text-transform: uppercase;
      padding: 20px;
      cursor: pointer;

      &::before, &::after {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: #8d8d8c;
        transform-origin: right;
        transition: transform 400ms ease;
        z-index: -1;
        transform: scaleX(0);

      }

      &:hover::before, &:hover::after {
        transform-origin: left;
        transform: scaleX(1);
      }

      &::after {
        z-index: -2;
        filter: blur(30px);
      }
    }
  }


}


.active {
  text-decoration: none;
  color: #bbb15c;

}

.active:hover {
  color: white;
}

