@import "../common/styles/sass/variables";

a{
  color: #cccccc;
  text-decoration: none;
}
.skillsBlock {
  width: 100%;

  .navSkillsBlock {
    max-width: 100%;
    text-decoration: none;
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    font-weight: 400;
    font-size: 30px;
    border-bottom: 1px solid transparent;
    line-height: 30px;
    margin: 30px;
    padding-top: 30px;
    overflow: hidden;

    .textEducation {
      position: relative;
      margin-left: 80px;


      &::before{
        content: '';
        width: 50px;
        height: 2px;
        left: 100%;
        top:50%;
        position: absolute;
        background: #bbb15c;
        transform: translate(30%, 50%);
      }
    }
    .textSkills {

      margin-left: 80px;
    }
    .active{
      text-decoration: underline;
      color: #bbb15c;
    }
  }
}

.textExperience{
  position: relative;

}
.textExperience::before{
    content: '';
    width: 50px;
    height: 2px;
    left: 100%;
    top:50%;
    position: absolute;
    background: #bbb15c;
    transform: translate(30%, 50%);
  }








